@import url("https://fonts.googleapis.com/css2?family=Inter:wght@100;200;300;400;500;600;700;800;900&display=swap");

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  
}


body {
  background-color: rgb(87, 42, 201);
  
}

body,
input,
textarea,
button {
  font-family: "Inter", sans-serif;
  box-shadow: 10px 10px;
   
}

.app {
  width: 400px;
  margin: auto;
  height: 100vh;
  
}

/* Contact.js */
.form {
  height: 100vh;
  display: flex;
  flex-direction: column;
  justify-content: center;
  border-radius: 25px;
}

.form > h1 {
  margin-bottom: 30px;
}

.form > input,
textarea {
  padding: 20px;
  border-radius: 10px;
  /* box-shadow: 0px 0px 5px rgba(0, 0, 0, 0.137); */
  margin-bottom: 20px;
  border: 1px solid lightgray;
  /* border: none; */
  background: #fff;
  font-size: 16px;
  color: rgb(0, 0, 32);
  outline: none;
}

.form > input:focus,
textarea:focus {
  border: 1px solid rgb(197, 32, 114);
}

.form > textarea {
  height: 150px;
  max-width: 400px;
  min-height: 100px;
}

.form > label {
  padding-bottom: 10px;
  color: rgb(0, 0, 32);
  font-weight: bold;
}

.form > button {
  padding: 20px;
  border: none;
  background-color: rgb(177, 36, 118);
  font-weight: 500;
  font-size: 20px;
  border-radius: 3px;
  color: rgb(194, 47, 162);
  cursor: pointer;
  transition: 0.2s ease-in-out;
  margin-top: 10px;
}

.form > button:hover {
  animation: myfirst 2s 1;
}

@keyframes myfirst
{
  0%      {background : linear-gradient(85deg,rgb(40, 245, 13) , rgb(4, 238, 255), rgb(252, 8, 8));}
  10%      {background : linear-gradient(85deg,rgb(40, 245, 13) , rgb(134, 248, 41), rgb(185, 241, 30));}
  30%      {background : linear-gradient(85deg,rgb(13, 94, 245) , rgb(201, 221, 223), rgb(252, 8, 8));}
  50%      {background : linear-gradient(85deg,rgb(13, 17, 245) , rgb(107, 252, 71), rgb(252, 8, 8));}
  60%      {background : linear-gradient(85deg,rgb(40, 245, 13) , rgb(4, 238, 255), rgb(248, 134, 134));}
  70%      {background : linear-gradient(85deg,rgb(40, 245, 13) , rgb(4, 238, 255), rgb(252, 8, 8));}
  80%      {background : linear-gradient(85deg,rgb(195, 245, 13) , rgb(4, 238, 255), rgb(252, 8, 8));}
  100%      {background : linear-gradient(85deg,rgb(40, 245, 13) , rgb(4, 238, 255), rgb(252, 8, 8));}




}



